@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .header {
      .container {
        .header__mainSearch {
          .search {
            margin-top: 40px;
          }
          .numbers {
            margin-top: 35px;
            ul {
              li {
                i {
                  width: 45px;
                  height: 45px;
                }
              }
            }
          }
          .workTime {
            margin: 40px 0 0 15px;
          }
        }
        .header__cabinet {
          margin-top: 40px;
          ul {
            li {
              &:nth-child(3) {
                width: 250px;
                i {
                  margin: 0 !important;
                }
              }
            }
          }
        }
      }
    }
    .content {
      .container {
        grid-template-columns: 25% 1fr 35%;
        .content__productCard__descr{
          .consultation{
            a{
              width: 250px;
            }
          }
        }
        .content__recomend{
          .slider {
            height: 290px;
            .lSSlideOuter {
              ul {
                .item-a,
                .item-b,
                .item-c,
                .item-e,
                .item-d,
                .item-f {
                  width: 235px;
                  height: 290px;
                }
                li {
                  img {
                    width: 150px;
                    height: 150px;
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts {
          width: 75%;
        }
      }
    }
  }
}
