@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
  }
  body .header .container .header__mainSearch .search {
    margin-top: 40px;
  }
  body .header .container .header__mainSearch .numbers {
    margin-top: 35px;
  }
  body .header .container .header__mainSearch .numbers ul li i {
    width: 45px;
    height: 45px;
  }
  body .header .container .header__mainSearch .workTime {
    margin: 40px 0 0 15px;
  }
  body .header .container .header__cabinet {
    margin-top: 40px;
  }
  body .header .container .header__cabinet ul li:nth-child(3) {
    width: 250px;
  }
  body .header .container .header__cabinet ul li:nth-child(3) i {
    margin: 0 !important;
  }
  body .content .container {
    grid-template-columns: 25% 1fr 35%;
  }
  body .content .container .content__productCard__descr .consultation a {
    width: 250px;
  }
  body .content .container .content__recomend .slider {
    height: 290px;
  }
  body .content .container .content__recomend .slider .lSSlideOuter ul .item-a,
  body .content .container .content__recomend .slider .lSSlideOuter ul .item-b,
  body .content .container .content__recomend .slider .lSSlideOuter ul .item-c,
  body .content .container .content__recomend .slider .lSSlideOuter ul .item-e,
  body .content .container .content__recomend .slider .lSSlideOuter ul .item-d,
  body .content .container .content__recomend .slider .lSSlideOuter ul .item-f {
    width: 235px;
    height: 290px;
  }
  body .content .container .content__recomend .slider .lSSlideOuter ul li img {
    width: 150px;
    height: 150px;
  }
  body .content .container .content__howToMakeOrder div .info {
    margin-left: 55px;
  }
  body .contentAddres iframe {
    height: 465px;
  }
  body .contentAddres .contentAddres__contactForm .comentary input[type*="text"] {
    width: 184px;
  }
  body .contentAddres .contentAddres__contactForm .comentary textarea {
    width: 395px;
  }
  body .contentAddres .contentAddres__contactForm .contacts {
    width: 75%;
  }
}
